import React from 'react';

class Register extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            email: '',
            password: '',
            name: ''
        }
    }// end of constructor

    

    onNameChange = (name) => {
        this.setState({name: name.target.value});
    }

    onEmailChange = (email) => {
        this.setState({email: email.target.value})
    }

    onPasswordChange = (password) => {
        this.setState({password: password.target.value})
    }


    onSubmitSignIn = () => {
        fetch('http://localhost:3000/Register', {method: 'POST', 
        headers: {'Content-Type': 'application/json'}, 
        body: JSON.stringify({email: this.state.email, password: this.state.password, name: this.state.name})})
        .then(response => response.json())
        .then(user =>{
            if(user){
                this.props.loadUser(user)
                this.props.onRouteChange('home');

            }
        })
        
    }


    render(props) {
        const {onRouteChange} = this.props;

        return (

            <article className="br4 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw5 shadow-5 center">
            <main className="pa4 black-80">
                <div className="measure">
                    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
                    <legend className="f1 fw6 ph0 mh0 center">Register</legend>
                        <div className="mt3">
                            <label className="db fw6 lh-copy f6" htmlFor="Name">Name</label>
                            <input 
                            className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
                            type="text" name="Name"  id="Name" onChange={this.onNameChange}/>
                        </div>
                        <div className="mt3">
                            <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
                            <input 
                            className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
                            type="email" name="email-address"  id="email-address" onChange={this.onEmailChange}/>
                        </div>
                        <div className="mv3">
                            <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
                            <input 
                            className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
                            type="password" name="password"  id="password" onChange={this.onPasswordChange}/>
                        </div>
                    
                        </fieldset>
                        <div className="">
                        <input onClick={this.onSubmitSignIn}
                        className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" 
                        type="submit" value="Register" />
                        
                        
                        </div>
                </div>
            </main>
            </article>
        );
    }// end of render

    
}// end of class

export default Register;