import React from 'react';

const ImageLinkForm = (props) => {

    return (


        <div>
            <p className="f4">Facerecognition Software</p>
            <div>
                <input className="f4 pa2 w-70 center" type="text" placeholder="Enter image URL" onChange={props.onInputChange} />
                <button className="f4 b pa2 br2 shadow-5 o-20" onClick={props.onButtonSubmit}>Detect</button>
            </div>
        </div>
    )
}

export default ImageLinkForm;
